.main-slider{
   margin-top: -101px;
}
@media(max-width: 1710px) {
   .main-slider{
      margin-top: -122px;
   }
}
@media(max-width: 991px) {
   .main-slider{
      margin-top: -75px;
   }
}
.main-slider__slide-img{
   position: absolute;
   top: 0;
   left: 0;
   width: 100vw;
   height: 100vh;
   object-fit: cover;
}
.main-slider__slide-gradient{
   position: absolute;
   top: 0;
   left: 0;
   background: linear-gradient(90deg, rgba(0, 0, 0, .6), hsla(0, 0%, 100%, 0));
   width: 55%;
   height: 100vh;
}
.main-slider__slide-wrapper{
   position: relative;
   z-index: 10;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: flex-start;
   height: 50vh;
   top: 30%;
   gap: 50px;
}
.main-slider__slide-wrapper > .logo-mobile{
   min-height: 186px;
}
.main-slider__slide-title{
   max-width: 677px;
}
.main-slider__container{
   height: 100vh;
}
.main-slider .swiper-pagination{
   display: flex;
   justify-content: center;
   align-items: center;
   bottom: 4.5%;
}
.main-slider .swiper-pagination .swiper-pagination-bullet{
   width: 10px;
   height: 10px;
   background: var(--body-bcolor);
   margin: 0 7.5px;
   opacity: 1;
}
.main-slider .swiper-pagination .swiper-pagination-bullet-active{
   position: relative;
   width: 18px;
   height: 18px;
   background: var(--body-bcolor);
}
.main-slider .swiper-pagination .swiper-pagination-bullet-active::after{
   content: '';
   position: absolute;
   top: calc(50% - 5px);
   left: calc(50% - 5px);
   width: 10px;
   height: 10px;
   border-radius: 50%;
   background: var(--body-color);
}
.main-slider__button-prev.swiper-button-prev{
   left: calc(50% - 133px);
}
.main-slider__button-next.swiper-button-next{
   right: calc(50% - 133px);
}
.main-slider__button-prev.swiper-button-prev,
.main-slider__button-next.swiper-button-next{
   top: 95%;
   background-color: var(--body-bcolor);
   width: 32px;
   height: 32px;
   border-radius: 50%;
}
.main-slider__button-prev.swiper-button-prev::after,
.main-slider__button-next.swiper-button-next::after{
   font-size: 16px;
   font-weight: bold;
   color: var(--body-color);
}
.booking {
   display: flex;
   flex-direction: column;
   position: absolute;
   top: 15%;
   right: 100px;
   z-index: 2;
   background-color: var(--body-bcolor);
   box-shadow: 0 1px 4px 0 var(--body-bscolor1);
   &__item {
      display: flex;
      cursor: pointer;
      position: relative;
      background-color: var(--body-bcolor);
      z-index: 1;
      border-bottom: 1px solid #ebebeb;
      transition: all .3s ease 0s;
      padding: 25px 20px;
   }
   &__item:hover{
      background-color: var(--body-hbcolor);
   }
   &__item.open{
      background-color: var(--body-hbcolor);
   }
   &__item.open~&__item-hidden{
      right: 346px;
      height: 752px;
      visibility: visible;
      opacity: 1;
   }
   &__icons {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      margin: 0 20px;
   }
   &__icons>.arrow{
      transform: rotate(90deg);
      margin-bottom: 3px;
   }
   &__content {
      max-width: 190px;
      margin: 0 20px;
   }
   &__content>*:not(:last-child){
      margin-bottom: 20px;
   }
   &__title {
      font-size: $f20;
      font-weight: 800;
      line-height: 1.4;
      }
   &__title--form {
      font-size: $f25;
      font-weight: 800;
      line-height: 1.2;
      }
   &__text {
      font-size: 13px;
      line-height: 1.3;
      max-width: 170px;
   }
   &__detail {
      font-weight: 600;
      color: #75baff;
   }
   &__detail>.arrow{
      display: none;
   }
   &__detail-arrow{
      display: none;
   }
   &__item-hidden{
      position: absolute;
      top: 0;
      right: 0;
      z-index: 0;
      visibility: hidden;
      opacity: 0;
      background-color: var(--body-hbcolor);
      height: 520px;
      padding: 25px 30px;
      transition: .5s;
   }
   &__form-description{
      color: #CA0713;
      margin-top: 10px;
   }
   &__form-description--gray{
      border-top: 1px solid var(--body-bscolor);
      color: var(--body-color);
      padding-top: 10px;
   }
   &__form{
      display: flex;
      flex-direction: column;
      position: relative;
      width: 230px;
      height: 100%;
   }
   &__form-fiels{
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      margin-top: 25px;
   }
   &__label{
      padding-left: 5px;
   }
   &__input{
      font-family: inherit;
      font-size: 15px;
      width: 100%;
      padding: 15px 0px 15px 15px;
      margin: 10px 0 10px -5px;
   }
   &__input--check{
      
   }
   &__input--check:checked ~ .test{
      display: block;
   }
   .test{
      display: none;
   }
   &__textarea{
      resize: vertical;
   }
   &__form-btn{
      font-family: inherit;
      font-size: 16px;
      background: linear-gradient(180deg, #75BAFF 0%, #6AA8E6 100%);
      padding: 15px 50px;
   }
}
@media (max-width: 1024px){
   .booking{
      position: static;
      top: 900px;
      right: 0;
      width: 100%;
      &__item{
         flex-wrap: wrap;
         align-items: center;
      }
      &__item.open~&__item-hidden{
         right: 0;
         overflow: visible;
         height: auto;
         padding: 30px;
      }
      &__icons>.arrow{
         display: none;
      }
      &__content{
         flex-grow: 1;
         flex-basis: 200px;
         max-width: unset;
      }
      &__text{
         max-width: unset;
      }
      &__content>&__detail{
         display: none;
         margin-right: 10px;
      }
      &__detail{
         margin-right: 10px;
      }
      &__detail-arrow{
         display: flex;
      }
      &__item-hidden{
         display: flex;
         justify-content: center;
         position: unset;
         overflow: hidden;
         transition: all .3s ease 0s;
         height: 0;
         padding: 0;
      }
      &__form{
      }
      &__form-btn{
         position: static;
         margin-top: 40px;
      }
   }
}
@media (max-width: 580px){
   .booking__icons{
      margin: 0;
   }
   .booking__content{
      margin: 0 0 0 20px;
   }
   .booking__content>.booking__detail{
      display: flex;
   }
   .booking__detail>.arrow{
      display: block;
      margin-left: 10px;
   }
   .booking__detail-arrow{
      display: none;
   }
}
@media (max-width: 991px){
   .main-slider__slide-wrapper{
      align-items: center;
      text-align: center;
      margin: auto 0;
      top: 20%;
   }
   .main-slider__slide-gradient{
      width: 100%;
   }
}
@media (max-width: 768px){
   .main-slider__slide-wrapper{
      justify-content: flex-start;
      top: 30%;
   }
   .main-slider__slide-wrapper > .logo-mobile{
      display: none;
   }
}
@media (max-width: 767px){
   .main-slider__button-prev.swiper-button-prev,
   .main-slider__button-next.swiper-button-next{
      display: none;
   }
}